import React from 'react'
import UserIcon from 'react-icons/lib/fa/user-md'
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap'
import { injectState, provideState } from 'reaclette'

const withState = provideState({
  initialState: () => ({
    dropdownOpen: false,
  }),
  effects: {
    toggle: () => state => ({
      ...state,
      dropdownOpen: !state.dropdownOpen,
    }),
  },
})

const AccountMenu = ({ effects, state }) => (
  <Dropdown
    nav
    direction="down"
    isOpen={state.dropdownOpen}
    toggle={effects.toggle}
  >
    <DropdownToggle
      nav
      caret
      style={{
        color: '#e2e3e4',
      }}
    >
      <UserIcon size="20" /> &nbsp; Profile
    </DropdownToggle>
    <DropdownMenu>
      <DropdownItem header>
        {state.serverInstance.auth.user.profile.data.email}
      </DropdownItem>
      <DropdownItem divider />
      <DropdownItem onClick={effects.logOut}>Déconnecter</DropdownItem>
    </DropdownMenu>
  </Dropdown>
)

export default withState(injectState(AccountMenu))
