import EyeIcon from 'react-icons/lib/fa/eye'
import PuzzleIcon from 'react-icons/lib/fa/puzzle-piece'
import React from 'react'
import {
  Badge,
  Card,
  CardBody,
  Button,
  CardTitle,
  CardText,
  CardImg,
  UncontrolledTooltip,
} from 'reactstrap'
import { injectState } from 'reaclette'
import ShareExamImg from '../imgs/search-exam.png'

const SearchExamCard = ({ history, state }) => (
  <Card style={{ boxShadow: '0 3px 5px rgba(0,0,0,.1)' }} className="h-100">
    <span
      onClick={() => {
        if (state.logged) {
          history.push('/contribuer')
        } else {
          history.push('/authenticate')
        }
      }}
      className="text-center"
      style={{
        backgroundColor: '#7bc3d1',
        color: 'white',
        display: 'block',
        cursor: 'pointer',
      }}
    >
      <h5 style={{ marginTop: '10px' }}>
        Chercher un examen&nbsp;
        <PuzzleIcon size="22" />
      </h5>
    </span>
    <CardImg
      top
      width="100%"
      src={ShareExamImg}
      style={{ cursor: 'pointer' }}
      alt="Card image cap"
      onClick={() => {
        history.push('/examen')
      }}
    />
    <CardBody>
      <CardText style={{ textAlign: 'center' }}>
        Vous avez passez un examen ? Pourquoi pas l'ajouter pour que vos
        succusseurs en bénéficieront, ou peut être vous le trouverez quand vous
        réviserez pour le concours. Vivez l'expérience du bénévolat et regardez
        l'ampleur de votre contribution <EyeIcon /> avec Med QCM
      </CardText>
    </CardBody>
  </Card>
)

export default injectState(SearchExamCard)
