import Cookies from 'js-cookie'
import React, { Fragment } from 'react'
import ContributeIcon from 'react-icons/lib/fa/puzzle-piece'
import GraduateIcon from 'react-icons/lib/fa/graduation-cap'
import EnvelopeIcon from 'react-icons/lib/fa/envelope'
import HomeIcon from 'react-icons/lib/fa/home'
import ConcoursIcon from 'react-icons/lib/fa/file-text-o'
import {
  Stitch,
  RemoteMongoClient,
  StitchServiceError,
  AnonymousCredential,
} from 'mongodb-stitch-browser-sdk'
import { Helmet } from 'react-helmet'
import {
  Badge,
  Button,
  Container,
  Collapse,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  Row,
  Col,
} from 'reactstrap'
import { ToastContainer, toast } from 'react-toastify'
import { HashRouter as Router, NavLink, Route, Switch } from 'react-router-dom'
import { provideState, injectState } from 'reaclette'
import { isEmpty } from 'lodash'

import Admin from './admin'
import AdminExams from './admin/admin-exams'
import AdminMessages from './admin/admin-messages'
import AdminReports from './admin/admin-reports'
import AuthOrCreate from './auth-or-create'
import AppIcon from './imgs/app-icon-brand.png'
import AccountMenu from './account-menu'
import Contribuate from './contribuate'
import ContactUs from './contact-us'
import ContestExam from './contest-exam'
import HomePage from './home'
import ConfirmPage from './confirm-page'
import ConfirmReset from './confirm-reset'
import Footer from './footer'
import LoadingIcon from './imgs/button-spinner.gif'
import PassExam from './pass-exam'
import Exam from './pass-exam/exam'

const withState = provideState({
  initialState: () => ({
    authId: undefined,
    authEmail: undefined,
    university: undefined,
    mongodb: undefined,
    serverInstance: undefined,
    userNotConfirmed: undefined,
    postedExams: undefined,
    adminLogged: false,
    lastPostedExams: undefined,
    noNeedToAuthenticate: false,
    authLoading: false,
    navbarCollapse: true,
  }),
  effects: {
    initialize: effects => async state => {
      state.authLoading = true
      const server = Stitch.initializeDefaultAppClient('med-dz-qcm-idjwm')
      state.serverInstance = server
      state.mongodb = server
        .getServiceClient(RemoteMongoClient.factory, 'mongodb-atlas')
        .db('med-dz-qcm')
        .collection('qcm')
      if (server.auth.user) {
        // logged
        if (server.auth.user.loggedInProviderName !== 'anon-user') {
          effects.signIn(server.auth.user.id)
        }
      } else {
        // user not logged
        try {
          const user = await server.auth.loginWithCredential(
            new AnonymousCredential()
          )
        } catch (error) {
          effects.handleError(error)
        }
      }
      await effects.handleAppQueryString()
      state.authLoading = false
      await effects.getLastPostedExams()
      // await state.mongodb.deleteMany({ creator: state.authId })
      // console.log(await state.mongodb.find({}).asArray())
    },
    setAuthEmail: (_, authEmail) => state => ({
      ...state,
      authEmail,
    }),
    setAdminLogged: effects => state => {
      state.adminLogged = true
      window.location.replace('/#/admin/exams')
    },
    logOut: () => state => {
      state.serverInstance.auth.logout()
      state.authId = undefined
      window.location.replace('/#/accueil')
    },
    getPostedExams: effects => async state => {
      try {
        state.postedExams = undefined
        const exams = await state.mongodb
          .find({ creator: state.authId })
          .asArray()
        state.postedExams = exams
      } catch (error) {
        effects.handleError(error)
      }
    },
    getLastPostedExams: effects => async state => {
      try {
        state.lastPostedExams = undefined
        const exams = await state.mongodb
          .find({ approved: true }, { limit: 7, sort: { date: -1 } })
          .asArray()
        state.lastPostedExams = exams
      } catch (error) {
        effects.handleError(error)
      }
    },
    handleAppQueryString: () => state => {
      const url = window.location.search
      const params = new URLSearchParams(url)
      const token = params.get('token')
      const tokenId = params.get('tokenId')

      if (token && tokenId) {
        state.noNeedToAuthenticate = true
        if (window.location.pathname === '/confirm') {
          const route = `${window.location.protocol}//${
            window.location.host
          }/#/confirm?token=${token}&tokenId=${tokenId}`
          window.location.replace(route)
        }

        if (window.location.pathname === '/reset') {
          const route = `${window.location.protocol}//${
            window.location.host
          }/#/reset?token=${token}&tokenId=${tokenId}`
          window.location.replace(route)
        }
      }
    },
    notifyError: (_, errorMessage) => state => {
      toast.error(errorMessage, {
        position: 'top-right',
        autoClose: 15000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      })
    },
    notifySuccess: (_, errorMessage) => state => {
      toast.success(errorMessage, {
        position: 'top-right',
        autoClose: 15000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      })
    },
    handleError: async (effects, error) => {
      console.log(error)
      if (error instanceof StitchServiceError) {
        await effects.notifyError(error.message)
      } else {
        await effects.notifyError(
          "Oops, something went wrong! Don't worry, our team is already on it"
        )
      }
    },
    getUserAccount: (effects, authId) => async state => {
      const university = Cookies.get('university') || 'Alger'
      state.university = university
      let user
      try {
        user = await state.mongodb
          .find({ type: 'user', authId, university })
          .asArray()
      } catch (error) {
        effects.handleError(error)
      }
      if (isEmpty(user)) {
        try {
          await state.mongodb.insertOne({ type: 'user', authId, university })
        } catch (error) {
          effects.handleError(error)
        }
      }
    },
    signIn: (effects, authId) => async state => {
      await effects.getUserAccount(authId)
      Cookies.set('authId', authId)
      state.authId = authId
      effects.getPostedExams()
    },
    toggleNavbar: () => state => ({
      ...state,
      navbarCollapse: !state.navbarCollapse,
    }),
  },
  computed: {
    logged: ({ authId }) => Boolean(authId),
  },
})

const App = ({ effects, state }) => (
  <div className="App">
    <Helmet>
      <title>Hakima QCM</title>
    </Helmet>
    <ToastContainer
      position="top-center"
      autoClose={20000}
      hideProgressBar={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnVisibilityChange
      draggable
      pauseOnHover
    />
    {state.authLoading ? (
      <Row className="my-3">
        <Col md={{ size: 2, offset: 5 }}>
          <br />
          <div className="text-center">
            <img src={LoadingIcon} width="50" height="50" alt="Loading..." />
          </div>
          <p className="text-muted text-center">
            Application en chargement ...
          </p>
        </Col>
      </Row>
    ) : (
      state.mongodb &&
      state.serverInstance && (
        <Router>
          <Row>
            <Col xs="12" md="12">
              <Navbar
                light
                expand="lg"
                style={{
                  backgroundColor: '#222534',
                  fontSize: '16px',
                  boxShadow: '0 3px 5px rgba(0,0,0,.1)',
                }}
              >
                <NavbarBrand href="#" style={{ margin: 0 }}>
                  <img src={AppIcon} alt="icon" height="50" width="60" />{' '}
                  <span
                    style={{
                      color: 'white',
                      fontSize: '22px',
                      verticalAlign: 'middle',
                    }}
                  >
                    Haki<strong style={{ color: '#7bc3d1' }}>ma</strong> QCM
                  </span>
                </NavbarBrand>
                <NavbarToggler
                  onClick={effects.toggleNavbar}
                  className="mr-2"
                  style={{ backgroundColor: '#e2e3e4' }}
                />
                <Collapse isOpen={!state.navbarCollapse} navbar>
                  <Nav className="ml-auto" navbar>
                    <NavItem active={false}>
                      <NavLink
                        className="nav-link"
                        to="/accueil"
                        style={{
                          color: '#e2e3e4',
                        }}
                        activeStyle={{ color: '#7bc3d1' }}
                      >
                        <HomeIcon size="20" /> Accueil
                      </NavLink>
                    </NavItem>
                    {state.logged && (
                      <NavItem>
                        <NavLink
                          className="nav-link"
                          to="/contribuer"
                          style={{
                            color: '#e2e3e4',
                          }}
                          activeStyle={{ color: '#7bc3d1' }}
                        >
                          <ContributeIcon size="20" /> Contribuer
                        </NavLink>
                      </NavItem>
                    )}
                    <NavItem>
                      <NavLink
                        className="nav-link"
                        to="/examen"
                        style={{
                          color: '#e2e3e4',
                        }}
                        activeStyle={{ color: '#7bc3d1' }}
                      >
                        <GraduateIcon size="20" /> Réviser un module
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className="nav-link"
                        to="/concours"
                        style={{
                          color: '#e2e3e4',
                        }}
                        activeStyle={{ color: '#7bc3d1' }}
                      >
                        <ConcoursIcon size="20" /> Passer un concours{' '}
                        <Badge color="warning">New</Badge>
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className="nav-link"
                        to="/contacteznous"
                        style={{
                          color: '#e2e3e4',
                        }}
                        activeStyle={{ color: '#7bc3d1' }}
                      >
                        <EnvelopeIcon size="20" /> Contacter nous
                      </NavLink>
                    </NavItem>
                    &nbsp;
                    {!state.logged && (
                      <Fragment>
                        <Button
                          color="light"
                          onClick={() =>
                            window.location.replace(
                              '/#/authenticate?toggle=createAccount'
                            )
                          }
                        >
                          S'inscrire
                        </Button>
                        &nbsp;
                        <Button
                          color="info"
                          onClick={() =>
                            window.location.replace(
                              '/#/authenticate?toggle=authentication'
                            )
                          }
                        >
                          Se connecter
                        </Button>
                      </Fragment>
                    )}
                    {state.logged && <AccountMenu size="20" />}
                  </Nav>
                </Collapse>
              </Navbar>

              <Switch>
                <Container fluid style={{ marginTop: '20px' }}>
                  <Row>
                    <Col md={{ size: 10, offset: 1 }} xs="12">
                      <Route exact path="/accueil" component={HomePage} />
                      <Route exact path="/" component={HomePage} />
                      <Route path="/admin" component={Admin} />
                      {state.adminLogged && (
                        <Fragment>
                          <Route path="/admin/exams" component={AdminExams} />
                          <Route
                            path="/admin/reports"
                            component={AdminReports}
                          />
                          <Route
                            path="/admin/messages"
                            component={AdminMessages}
                          />
                        </Fragment>
                      )}
                      <Route path="/confirm" component={ConfirmPage} />
                      <Route path="/contacteznous" component={ContactUs} />
                      <Route path="/concours" component={ContestExam} />
                      <Route path="/reset" component={ConfirmReset} />
                      <Route path="/authenticate" component={AuthOrCreate} />
                      <Route exact path="/examen" component={PassExam} />
                      <Route path="/examen/:examId" component={Exam} />
                      {(state.adminLogged || state.logged) && (
                        <Fragment>
                          <Route
                            exact
                            path="/contribuer"
                            component={Contribuate}
                          />
                          <Route
                            path="/contribuer/:examId"
                            component={Contribuate}
                          />
                        </Fragment>
                      )}
                    </Col>
                  </Row>
                </Container>
              </Switch>
            </Col>
          </Row>
        </Router>
      )
    )}
    <Footer />
  </div>
)

export default withState(injectState(App))
