import React, { Fragment } from 'react'
import WorkBench from 'react-icons/lib/fa/wrench'
import { Nav, Navbar, NavbarBrand, NavItem, Row, Col } from 'reactstrap'
import { injectState, provideState } from 'reaclette'
import { NavLink } from 'react-router-dom'

import AdminAuth from './admin-authentication'

const withState = provideState({
  initialState: () => ({}),
  effects: {
    initialize: () => (state, { history }) => {},
  },
})

const Admin = ({ effects, state, history }) => (
  <Row>
    <Col>
      {state.adminLogged && (
        <Navbar
          light
          collapse
          expand="md"
          style={{
            fontSize: '16px',
            boxShadow: '0 3px 5px rgba(0,0,0,.1)',
            backgroundColor: 'white',
          }}
        >
          <NavbarBrand href="#">
            <WorkBench color="orange" /> &nbsp;
            <span style={{ fontWeight: 'bold' }} className="text-muted">
              Hakima QCM Administration
            </span>
          </NavbarBrand>
          <Nav className="ml-auto" navbar>
            <NavItem active={false}>
              <NavLink className="nav-link" to="/admin/exams">
                Approbation d'examns
              </NavLink>
            </NavItem>
            <NavItem active={false}>
              <NavLink className="nav-link" to="/admin/reports">
                Signalisation d'examens
              </NavLink>
            </NavItem>
            <NavItem active={false}>
              <NavLink className="nav-link" to="/admin/messages">
                Messages utilisateurs
              </NavLink>
            </NavItem>
          </Nav>
        </Navbar>
      )}

      {!state.adminLogged && (
        <Row>
          <Col md={{ size: 4, offset: 4 }}>
            <AdminAuth onAdminLogged={effects.setAdminLogged} />
          </Col>
        </Row>
      )}
    </Col>
  </Row>
)

export default withState(injectState(Admin))
